import { React, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import {
  loginUser, registerUser,
  usrenameUniqueCheck, emailUniqueCheck
} from "./Auth_Action"
import "./RegisterPage.css"

const RegisterPage = () => {
  const navigate = useNavigate()

  const passwordRegex = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/
  const emailRegex = /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/
  // State
  const [username, setUsername] = useState('')
  const [email, setEmail] = useState('')
  const [name, setName] = useState('')
  const [password, setPassword] = useState('')
  const [confirm, setConfirm] = useState('')

  //   
  const [usernameMsg, setUsernameMsg] = useState('')
  const [emailMsg, setEmailMsg] = useState('')
  const [nameMsg, setNameMsg] = useState('')
  const [passwordMsg, setPasswordMsg] = useState('')
  const [confirmMsg, setConfirmMsg] = useState('')

  //  
  const [isUsername, setIsUsername] = useState(false)
  const [isEmail, setIsEmail] = useState(false)
  const [isName, setIsName] = useState(false)
  const [isPassword, setIsPassword] = useState(false)
  const [isConfirm, setIsConfirm] = useState(false)

  // Check
  const [checkUsername, setCheckUsername] = useState(false)
  const [checkEmail, setCheckEmail] = useState(false)

  const onChangeUsername = (e) => {
    setUsername(e.currentTarget.value)
    if (e.currentTarget.value.length < 4 || e.currentTarget.value.length > 16) {
      setUsernameMsg('4  16  .')
      setIsUsername(false)
    } else {
      setUsernameMsg('')
      setIsUsername(true)
    }
  }

  const onChangeEmail = (e) => {
    const currentEmail = e.currentTarget.value
    setEmail(currentEmail)
    if (!emailRegex.test(currentEmail)) {
      setEmailMsg('   .')
      setIsEmail(false)
    } else {
      setEmailMsg('')
      setIsEmail(true)
    }
  }

  const onChangeName = (e) => {
    setName(e.currentTarget.value)
    if (e.currentTarget.value.length < 2 || e.currentTarget.value.length > 5) {
      setNameMsg('2  5  .')
      setIsName(false)
    } else {
      setNameMsg('')
      setIsName(true)
    }
  }

  const onChangePassword = (e) => {
    const currentPassword = e.currentTarget.value
    setPassword(currentPassword)
    if (!passwordRegex.test(currentPassword)) {
      setPasswordMsg('++  8  .')
      setIsPassword(false)
    } else {
      setPasswordMsg('')
      setIsPassword(true)
    }
  }

  const onChangeConfirm = (e) => {
    const currentConfirm = e.currentTarget.value
    setConfirm(currentConfirm)
    if (password === currentConfirm) {
      setConfirmMsg('')
      setIsConfirm(true)
    } else {
      setConfirmMsg('  .')
      setIsConfirm(false)
    }
  }

  const onClickCheckUsername = (e) => {
    let body = { username: username }
    usrenameUniqueCheck(body, setCheckUsername)
  }

  const onClickCheckEmail = (e) => {
    let body = { email: email }
    emailUniqueCheck(body, setCheckEmail)
  }

  const onClickRegister = (e) => {
    let body = {
      username: username,
      email: email,
      first_name: name,
      password: password,
      password2: confirm,
    }
    if (window.confirm(" ?")) {
      registerUser(body, navigate)
    }
  }

  return (
    <>
      <div className="registerBox">
        <div><h2></h2></div>
        <br />
        <label></label>
        <div className="checkBoxContain">
          <input type="text" value={username}
            disabled={checkUsername}
            placeholder=""
            onChange={onChangeUsername} />
          <button onClick={onClickCheckUsername}></button>
        </div>
        <div className="validMsg">{usernameMsg}</div>

        <label></label>
        <div className="checkBoxContain">
          <input type="text" value={email}
            disabled={checkEmail}
            placeholder=""
            onChange={onChangeEmail} />
          <button onClick={onClickCheckEmail}></button>
        </div>
        <div className="validMsg">{emailMsg}</div>

        <label></label>
        <input type="text" value={name}
          placeholder=""
          onChange={onChangeName} />
        <div className="validMsg">{nameMsg}</div>

        <label></label>
        <input type="password" value={password}
          placeholder=""
          onChange={onChangePassword} />
        <div className="validMsg">{passwordMsg}</div>

        <label> </label>
        <input type="password" value={confirm}
          placeholder=" "
          onChange={onChangeConfirm} />
        <div className="validMsg">{confirmMsg}</div>
        <br />

        {isUsername && isName && isEmail && isPassword && isConfirm && checkUsername && checkEmail
          ? <button disabled={false} onClick={onClickRegister} ></button>
          : <button disabled={true} ></button>}

        <br />
        {<Link to="/login">
            
        </Link>}
      </div>
    </>
  )
}

export default RegisterPage